button {
&.slick-btn {
  width: 45px;
  height: 45px;
  border-right: 3px solid #ffffff;
  border-top: 3px solid #ffffff;
  opacity: 0.5;
  z-index: 1;
  background-color: transparent;
  outline: none;
  border-left: none;
  border-bottom: none;
  &.slick-next {
    transform: rotate(45deg);
  }
  &.slick-prev {
    transform: rotate(225deg);
  }
  &.slick-disabled {
    display: none;
  }
  // position: absolute;
  // top: 95px;
  // left: 1730px;
}
}
.slick-btn:hover {
  border-right: 3px solid #d3c1ad;
  border-top: 3px solid #d3c1ad;
}
.slick-slider {
  max-width: 1920px;
  margin: 0 auto;
}
#slick-slide01 {
  background-image: url('../img/back1.jpg');
  background-position: center;
  background-size: 100% 100%;
}
.slick-dots {
  position: absolute;
  bottom: -250px;
  text-align: center;
  width: 100%;
  padding: 0;
  margin: 0;
  list-style: none;
  & li {
    display: inline-block;
    position: relative;
    &:before {
      content: "";
      display: block;
      position: relative;
      width: 11px;
      height: 11px;
      cursor: pointer;
      outline: none;
      border-radius: 50%;
      background-color: #ffffff;
      opacity: 0.5;
      margin: 0 7px;
      padding: 0;
    }
    &:hover {
      &:before {
        border: 2px solid #c5a379;
        background-color: transparent;
        opacity: 0.7;
        margin: 0 5px;
        top: 1px;
      }
    }
    & button {
      display: none;
    }
    // &.slick-active {
    //   &:before {
    //     top: 5px;
    //     width: 40px;
    //     height: 40px;
    //     opacity: 1;
    //     margin: 0 2px;
    //   }
    // }
  }
}
